.container {
  display: flex; }
  .container__block {
    padding: 20px;
    box-sizing: border-box;
    height: 100vh; }
  @media (max-width: 680px) {
    .container {
      flex-direction: column; } }

.page__body {
  font-family: Arial, sans-serif;
  background-color: #EEEEEE;
  margin: 0;
  padding: 0;
  color: #292D32; }

.sidebar {
  width: 306px;
  background-color: #FFFFFF;
  color: #000000; }
  .sidebar__title {
    margin-bottom: 20px; }
  .sidebar__menu {
    list-style: none;
    padding: 0; }
  .sidebar__link {
    border-radius: 10px;
    display: block;
    padding: 20px;
    color: #9197B3;
    text-decoration: none;
    position: relative; }
    .sidebar__link:hover {
      color: #FFFFFF;
      background-color: #5932EA; }
  .sidebar__logo {
    display: inline-block; }
  .sidebar__manager, .sidebar__rating, .sidebar__experience {
    margin: 5px 0; }
  @media (max-width: 680px) {
    .sidebar {
      width: 100%;
      height: auto; } }

.content {
  flex: 1; }
  .content__header {
    background-color: #fff;
    padding: 20px;
    margin-bottom: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); }
    .content__header__title {
      margin: 0; }
  .content .table-container {
    background-color: #fff;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); }
    .content .table-container__title {
      margin-bottom: 20px; }
    .content .table-container .table {
      width: 100%;
      border-collapse: collapse;
      margin-bottom: 20px; }
      .content .table-container .table__header {
        background-color: #f4f4f9; }
        .content .table-container .table__header__cell {
          padding: 15px;
          text-align: left;
          border: 1px solid #ddd; }
      .content .table-container .table__row:nth-child(even) {
        background-color: #f9f9f9; }
      .content .table-container .table__row__cell {
        padding: 15px;
        text-align: left;
        border: 1px solid #ddd; }
        .content .table-container .table__row__cell--active {
          background-color: #e0f7e0; }
        .content .table-container .table__row__cell--inactive {
          background-color: #f7e0e0; }
  @media (max-width: 680px) {
    .content {
      padding: 10px; }
      .content .table-container {
        padding: 10px; }
        .content .table-container .table__cell {
          padding: 10px; } }
  @media (max-width: 320px) {
    .content {
      padding: 5px; }
      .content__header {
        padding: 10px; }
      .content .table-container {
        padding: 5px; }
        .content .table-container__title {
          font-size: 1.2em; }
        .content .table-container .table__header__cell {
          padding: 8px;
          font-size: 0.8em; }
        .content .table-container .table__row__cell {
          padding: 8px;
          font-size: 0.8em; } }
